<template>
  <div class="wrapper">
    <div class="title">
      发表评论
    </div>
    <div>
      <input type="hidden" name="" :value="parentUser" />
      <button @click="quet" v-if="$store.state.isShow">取消回复</button>
    </div>
    <div class="inputs">
      <p><input type="text" v-model="name" placeholder="昵称" /></p>
      <p>
        <input
          type="text"
          v-model="email"
          placeholder="邮箱（评论被回复时你能收到通知）"
        />
      </p>
      <p>
        <textarea
          cols="30"
          rows="10"
          v-model="comment"
          placeholder="无意义的内容我可能不会回复你的"
        ></textarea>
      </p>
      <p>
        <input
          type="text"
          v-model="verification"
          class="verification"
          placeholder="验证码"
        />
        <img
          src="/api/captcha"
          @click="imgCilck"
          alt=""
          id="captcha"
        />
      </p>
      <p>
        <button class="commit" @click="commit">提交留言</button
        ><button class="reset" @click="reset">重置</button>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      comment: "",
      verification: "",
      parentUser: "",
      imgData: "",
    };
  },
  props: ["isShow"],
  mounted() {
    // this.imgCilck();
  },
  methods: {
    reset() {
      this.name = "";
      this.email = "";
      this.comment = "";
      this.verification = "";
    },
    imgCilck() {
      // location.reload();
      // fetch("http://127.0.0.1:12306/api/captcha")
      //   .then((resp) => resp.blob())
      //   .then((resp) => {
      //     this.imgData = resp;
      //   });
      document.getElementById("captcha").src =
        "/api/captcha?a=" + Math.random();
    },
    commit() {
      console.log(this.$route);
      let blogid = null;
      if (this.$route.name === "About") {
        blogid = -1;
      }
      if (this.$route.name === "Guestbook") {
        blogid = -2;
      }
      if (this.$route.name === "Details") {
        blogid = +this.$route.params.id;
      }
      let parentName = this.$store.state.chatUser.parentUser;
      let parentid = this.$store.state.chatUser.parentId;
      let userName = this.name;
      let email = this.email;
      let captcha = this.verification;
      let content = this.comment;
      fetch("/api/comment", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({
          userName,
          parentName,
          captcha,
          content,
          email,
          parentid,
          blogid,
        }),
      })
        .then((resp) => resp.json())
        .then((resp) => {
          console.log(resp);
        });
        // location.reload();
    },
    quet() {
      this.$store.commit("getIsShow", false);
      this.$store.commit("getChatUser", { parentUser: "管理员", parentId: -1 });
    },
  },
};
</script>

<style lang="less">
.wrapper {
  width: 100%;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
  border-radius: 4px;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
  p {
    margin: 15px 0;
    line-height: 34px;
    img {
      vertical-align: top;
      margin-left: 10px;
      cursor: pointer;
    }
    .verification {
      width: 30%;
    }
  }
  button {
    outline: none;
    border-radius: 4px;
    border: 1px solid #ccc;
    background-color: #fff;
    padding: 6px 12px;
    margin-right: 5px;
    font-size: 14px;
    cursor: pointer;
  }
  input,
  textarea {
    width: 100%;
    height: 34px;
    border-radius: 4px;
    border: 1px solid #ccc;
    text-indent: 1em;
    line-height: 34px;
    font-size: 14px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    &::placeholder {
      color: #aaa;
    }
    &:focus {
      color: #66afe9;
      outline: none;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
        0 0 8px rgba(102, 175, 233, 0.6);
    }
  }
}
</style>
