<template>
  <div class="msg">
    <div class="title">
      <img src="@/assets/1.jpg" alt="" /><span>{{ item.userName }}</span
      >:发表于{{  date(item.ctime)
      }}<span
        class="chat"
        @click="chatClick({ parentUser: item.userName, parentId: item.id })"
        >[回复]</span
      ><span>{{ item.parentName }}</span>
    </div>
    <p>{{ item.content }}</p>
    <div class="children" v-if="item.children">
      <div
        class="child-msg"
        v-for="(prop, index) in item.children"
        :key="index"
      >
        <div class="title">
          <img src="@/assets/1.jpg" alt="" /><span>{{ prop.userName }}</span
          >:发表于{{ date(prop.ctime)
          }}<span
            class="chat"
            @click="chatClick({ parentUser: prop.userName, parentId: item.id })"
            >[回复]</span
          >
          <span>{{ prop.parentName }}</span>
        </div>
        <p>{{ prop.content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Message",
  props: ["item"],
  computed: {
    
  },
  methods: {
    chatClick(result) {
      this.$store.commit("getIsShow", true);
      this.$store.commit("getChatUser", result);
    },
    date(time) {
      let date = new Date(time * 1000);
      let year = date.getFullYear();
      let month = +date.getMonth() + 1;
      let day = date.getDate();
      let h = date.getHours();
      let m = date.getMinutes();
      let s = date.getSeconds()
      return `${year}年${month}月${day}日${h}点${m}分${s}秒`;
    },
  },
};
</script>

<style lang="less">
.msg {
  margin-bottom: 10px;
  border: 1px dashed #7ca4c1;
  background-color: #d9edf7;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 4px;
  & > p {
    color: #31708f;
    font-size: 15px;
    text-indent: 2em;
    margin: 5px 0;
  }
  .children {
    margin-left: 20px;
    & p {
      color: #31708f;
      font-size: 15px;
      text-indent: 2em;
      margin: 5px 0;
    }
  }
  div.title {
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      vertical-align: top;
    }
    .chat {
      cursor: pointer;
      margin: 0 3px;
    }
    line-height: 40px;
    font-size: 14px;
    color: #31708f;
    span {
      font-weight: 600;
    }
  }
}
</style>
